.video {
    padding-bottom: 200px;

  }
  .video-quote {
    height: 200px;
    width: 100%;
    background-color: #F8F8F8;

    &__text {
        font-size: 36px;
        font-style: italic;
        line-height: 40px; 
        letter-spacing: 0.6px;
        display: flex;
        flex-direction: column;
        align-items: center;
       padding-top: 80px;

        & span {
            font-size: 14px;
            font-style: italic;
            font-weight: 300;
            line-height: 18px;
            letter-spacing: 0.6px;
            margin-left: 600px;
        }
    }
  }
  .video-content {
    padding: 200px 0;
    margin-left: 50px;
    &__img {
      max-width: 100%;
      height: auto;
    }
  }
  .video-stats {
      display: flex;
     justify-content: center;
     gap: 100px;
      text-align: center;

    &__num{
        color: #01353E;
        font-size: 64px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.6px;
    }
    &__text{
        font-weight: 300;
        line-height: 18px; 
        letter-spacing: 0.6px;
    }
  }
  
  @media(max-width:990px){
    .video-content{
      padding-right: 30px;
    }
  }
  @media(max-width:768px){

    .video{
      padding-bottom: 50px;
    }

    .video-content{
      padding: 50px 0;
      padding-right: 30px;
    }
    .video-quote__text{
      font-size: 28px;
    }

    .video-quote__text span{
      margin-left: 0;
    }

    .video-stats{
      display: block;
    }

    .video-stats__item{
      margin-bottom: 60px;
    }
  }

  @media(max-width:440px){
    .video-quote__text{
      padding-left: 50px;
  }

  }
