.slider {
    
    &__wrapper {
        background-image: url('../images/slider-bg.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 800px;

        display: flex; 
        flex-direction: column; 

    }
    &__container {
        height: 100%;
        
    }
    &__inner {
        position: relative;
        color: #FFF;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding-left: 600px;
        padding-bottom: 50px;
        
    }
    &__title {
        margin-bottom: 230px;
        padding-left: 55px;
    }
    &__subject {
        font-size: 48px;
        font-style: normal;
        line-height: 56px; 
        letter-spacing: 0.6px;

        max-width: 600px;
    }
    &__subtitle {
        font-weight: 300;
        line-height: 18px; 
        letter-spacing: 0.6px;
    }
  }
  .slider-slick {
    display: flex;
    align-items: flex-end;
    gap: 15px;
    position: absolute;
    z-index: 2;
    padding-left: 450px;
    padding-bottom: 77px;
    height: 83%;
    width: 80%;
    
    &__btn {
        background-color: #fff;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        font-size: 0;
        padding: 0;
        border: none;
    }

    &__btn--active {
        background-color: #00B2A0;
    }
  }
  
  
  @media (max-width:1230px) {
    .slider__inner{
        padding-left: 100px;
       }
       .slider__subject{
        font-size: 38px;
        max-width: 440px;
       }

       .slider-slick{
        padding-left:100px;
        padding-bottom: 0;
       }
}
@media (max-width:768px) {
    .slider-slick li:nth-child(10){
        display: none;
    }
    .slider-slick li:nth-child(9){
        display: none;
    }
  }