.school {
    padding: 200px 0 200px;
    background-color: #F8F8F8;
    &__inner {
        display: flex;
        justify-content: space-between;
        margin: 100px 0 100px;
        
    }
    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }
    &__title {
       display: flex;
       align-items: center;
       font-size: 24px;
       line-height: 28px; 
       letter-spacing: 0.6px;
      
    }

    &__img {
        margin-right: 10px;
    }

    &__text {
        margin-top: 15px;
        width: 350px;
        font-weight: 300;
        line-height: 18px; 
        letter-spacing: 0.6px;
    }
  }
  .school-info {
    display: flex;
    justify-content: space-between;
    
    &__stats {
        & p {
            font-weight: 300;
            line-height: 18px; 
            letter-spacing: 0.6px;
            margin-bottom: 18px;
        }
    }

     &__num {
        display: flex;
        align-items: center;
      
        & span {
            font-size: 24px;
            line-height: 18px; 
            letter-spacing: 0.6px;
            margin-left: 10px;

        }
     }


    &__text {
        width: 600px;
        font-weight: 300;
        line-height: 18px;
        letter-spacing: 0.6px;
        margin-right: 20px;
        

        & p:not(:last-child) {
            margin-bottom: 30px;
        }
    }
    
  }
  
 .icon {

    &__85 {

        display: inline-block;
        background-image: url('../images/85.svg');
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
        width: 200px;
        height: 15px;
    }

    &__95 {
        display: inline-block;
        background-image: url('../images/95.svg');
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
        width: 247px;
        height: 15px;
    }

    &__99 {
        display: inline-block;
        background-image: url('../images/99.svg');
        background-size: auto;
        background-position: center;
        background-repeat: no-repeat;
        width: 293px;
        height: 15px;
    }

 } 
  
 @media(max-width:1050px) {
    .school__wrap{
      margin-bottom: 50px;
    }
    .school__inner{
        display: block;
        text-align: center;
    }
    .school-info__stats{
        padding-right: 15px;
    }
 }
 @media(max-width:768px) {

    .school{
        padding: 100px 0 100px;
    }
    .school-info{
        display: flex;
         align-items: center;
    }
   
 }
 @media(max-width:660px) {
    .school-info{
        display: flex;
        flex-direction: column;
       
    }
    .school-info__stats{
        padding-left: 20px;
    }
    .school-info__text{
       width: 300px;
       text-align: center;
       
        
    }
 }