.connection {
    padding: 200px 0 200px;
    &__title{
        margin-bottom: 240px;
    }
    &__map {
         margin-bottom: 200px;

    }
    &__img {
      max-width: 100%;
      height: auto;
    }
    &__inner {
        display: flex;
    }
    &__social {
        display: flex;
        gap: 10px;
        justify-content: center;
        padding-top: 200px;
    }
    &__link {
        font-size: 24px;
        line-height: 18px; 
        letter-spacing: 0.6px;
        transition: color .3s;
        &:hover{
            color: #00B2A0;
        }
    }
  }

  .form {
   display: flex;
   flex-direction: column;
   width: 570px;
   & input::placeholder{
    color: #00B2A0;
   }
   & textarea::placeholder,
   & input{
    display: block;
    width: 100%;
    border: none;
    border-bottom: 2px solid #DADADA;
    outline: none;
    padding: 16px 0;
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 18px; 
    letter-spacing: 0.6px;
   }

   & textarea {
    min-height: 140px;
    width: 100%;
    border: none;
    outline: none;
    resize: none;
   }

   & textarea::placeholder{
    height: 90px;
    color: #00B2A0;
   }
   & button {
    margin-top: 70px;
    background-color: transparent;
    width: 350px;
    padding: 24px 111px 24px 111px;
    border: 1px solid #C4C4C4; 
    font-size: 24px;
    line-height: 18px; 
    letter-spacing: 0.6px;
   }
  }
  .connection-contact {
    background-color: #F8F8F8;
    width: 600px;
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    &__item {  
     font-size: 24px;
     font-style: normal;
     font-weight: 400;
     line-height: 30px; 
     letter-spacing: 0.6px;
     padding-left: 50px;
    }
  }

  .tell{
    transition: color .3s;
  }
  .tell:hover{
    color: #00B2A0;
  }


  @media(max-width:768px){
    .connection{
      padding: 100px 0 100px;
    }
    .connection-contact{
      margin-top: 50px;
      width: 300px;
      margin-left: auto;
      margin-right: auto;
    }
    .connection__map{
      margin-bottom: 100px;
    }
    .connection__title{
      margin-bottom: 100px;
    }
    .connection-contact__item{
      font-size: 18px;
      padding-bottom: 20px;
      padding-left: 20px;
      text-align: center;
    }
    .connection__social{
      padding-top: 100px;
    }

    .connection__inner{
      display: flex;
      flex-direction: column;
    }
    .form{
     width: 100%;
    }
    .form button {
     margin-left: auto;
     margin-right: auto;
    }
  }

  @media(max-width:768px) {
    .connection__social{
      gap: 30px;
    }
    .connection__link{
      font-size: 18px;
    }
  }