.services {
    background-color: #F8F8F8;
    padding: 200px 0 200px;
    &__inner {
        display: flex;
        justify-content: space-between;
    }
    &__title {
        margin-bottom: 212px;
    }
  }
  .services-column {
    background-color:var(--white-color);
    width: 360px;
    height: 577px;
    display: flex;
    flex-direction: column; 
    justify-content: space-between; 
    &__title {

      color: var(--primary-color);
      font-size: 36px;
      font-style: normal;
      line-height: 40px;
      text-align: center;
      margin-top: 45px;
      margin-bottom: 60px;
      
    }
    &__list {
      line-height: 18px;
      letter-spacing: 0.6px; 
      margin-bottom: 20px;
      padding-left: 43px;
      &:last-child{
        margin-bottom: 0;
      }
    }

    &__wrap {
      color: var(--primary-color);
      font-size: 36px;
      font-style: normal;
      line-height: 40px;
      display: flex;
      justify-content: center;
      padding-top: 50px;
      padding-bottom: 45px;
      
    }

    &__item--padding{
      padding-bottom: 75px;
    }
    &__price {
      display: flex;
      align-items: center;
      transition: color .4s;

      
      & sup {
        font-size: 18px;
        line-height: 22px;
        
      }

      &:hover{
        color: #00B2A0;
      }
    }
  }
  

  @media(max-width:1200px){

    .services{
      padding: 100px 0 100px;
    }

    .services__title{
      margin-bottom: 100px;
    }
    .services__inner{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .services-column{
      margin-bottom: 60px;
    }
  }