.header {
  width: 100%;
  transition: background-color var(--bs-transition);
  z-index: 50;

  .header-is-sticky & {
    // ..стили для липкого хедера
    position: fixed;
    background-color: #007aff;
    
  }

  // .header__container
  &__container {
   

    .header-is-sticky & {
      height: rem($headerHeight - 50);
    }
  }
}



.header-top {
  position: relative;
  height: 1080px;
  
   &__btn {
    display: none;
    background-color: transparent;
   }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, #01353E 0%, rgba(215, 209, 209, 0.6) 31.64%, rgba(51, 51, 51, 0.22) 74.35%, rgba(51, 51, 51, 0.00) 99.87%);
    z-index: 1;
    
  }
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../images/bg-img.jpg');
   
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 0;
  }
  &__inner {
    margin-top: 30px;
    display: flex;
    position: relative;
    z-index: 2;
    align-items: center;
  }

  &__list {
    display: flex;
    gap: 45px;
    margin-left: 195px;    
  }
 
  &__link {
    position: relative;
    font-size: 24px;
    letter-spacing: 0.6px;
    color: var(--white-color);
    padding-bottom: 5px;

    &::after {
      position: absolute;
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background-color: #00B2A0;
      left: 0;
      bottom: 0;
      transform: scale(0);
      transition: all 280ms ease 0ms;
    }
    &:hover::after{
      transform: scale(1);
    }
  }
}





.header-content {
  z-index: 2;
  position: relative;
  text-align: center;
  &__wrap {
    margin-top: 290px;
  }
  
  &__title {
    font-family: raleway;
    color: #0D302C;
    font-size: 200px;
    font-weight: 700;
    letter-spacing: 0.6px;
  }
  &__subtitle {
    font-size: 24px;
    color: #000;
    padding-bottom: 315px;
  }
 
}
@media (max-width: 1200px) {

  .header-top__list {
    margin-left: 140px;
  }
}
@media (max-width: 992px) {
  .header-top__list {
    margin-left: 80px;
  }
  .header-content__title{
    font-size: 150px;
  }
  .header-content__subtitle{
    font-size: 20px;
  }

  .header-top__link{
    font-size: 18px;
  }
}

@media (max-width: 768px) {

  .header-top__list {
    margin-left: 80px;
    display: none;
    
  }
  .header-top__link{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    display: block;
    text-align: center;
    transform: translateY(-300%);
  }
  .header-top__btn{
   display: block;
   cursor: pointer;
   position: relative;
  
  }
  .header-top__inner{
    display: flex;
    justify-content: space-between;
  }
  
}

@media (max-width: 660px) {
  .header-content__title{
    font-size: 130px;
  }
}
@media (max-width: 440px) {
  .header-content__title{
    font-size: 100px;
  }

  .header-content__subtitle{
    font-size:14px;
  }
}





