.gallery {
    padding: 200px 0 200px;

  
    &__title{
        margin-bottom: 212px;
    }

    &__inner {
        padding-left: 30px;
    }

    &__button {
        display: flex;
        justify-content: center;
        padding-right: 75px;
    }
    &__btn {
        border: 1px solid #C4C4C4;
        background-color: transparent;
        padding: 23px 81px;
        
        
        font-size: 24px;
        line-height: 18px; 
        letter-spacing: 0.6px;
     
    
    }
}

.gallery-top {
   
    &__btn {
        margin-bottom: 50px;
        font-size: 24px;
        margin-right: 20px;
        background-color: transparent;
        line-height: 18px; 
        letter-spacing: 0.6px;
        transition: color 0.4s;

        &:hover {
            color: #00B2A0;
        }

        &:last-child{
            margin-right: 0;
        }
    }
  }
  .gallery-content {
    display: flex;
    flex-wrap: wrap;
    &__item {
        margin-bottom: 40px;
        margin-right: 40px;

        &:nth-child(3) {
            margin-right: 0;
        }
        &:nth-child(6) {
            margin-right: 0;
        }
    }
  }
  
//   @media(max-width:1270px){
//     .gallery-content{
//        display: block;
//        column-count: 2;
//     }
//   }

  @media(max-width:990px){
    .gallery-content{
        display: block;
        column-count: 3;
    }
    .gallery-content__item img {
        max-width: 100%;
        height: auto;
    }
   
    .gallery__button{
        padding-top: 50px;
    }
    .gallery-content__item{
        margin-right: 0;
    }
  }

  @media(max-width:768px){
    .gallery{
        padding: 100px 0 100px;
    }
    .gallery__button{
        padding-left: 50px;
     }

    .gallery__title{
        margin-bottom: 50px;
    }

    .gallery-content{
        column-count: 2;
    }
    .gallery-top__btn{
        font-size: 18px;
    }
   

   
  }

  @media(max-width:440px){
    .gallery-content{
        column-count: 1;
        text-align: center;
    }

   
    .gallery__btn{
        padding: 10px 60px;
        font-size: 18px;
        font-weight: 700;
    }

    .gallery__title{
        padding-left: 20px;
    }

    

   
  }