.about {
    padding-top: 190px;
    padding-bottom: 200px;
    &__inner {
        margin-top: 50px;
        display: flex;
        
       
    }


  }
 
  .about-column {
      
      color: var(--bs-font-color);
      font-weight: 300;
      line-height: 18px; 
      letter-spacing: 0.6px;
    & p:not(:last-child) {
        margin-bottom: 30px;
    }

    & p {
        max-width: 490px;
    }

    &__margin{
        margin-top: 65px;
        padding-bottom: 50px;
        
    }

    &__last{
        margin-left: 120px;
    }

  }
  
  @media(max-width: 768px) {

    .about{
        padding: 100px 0 100px;
    }
    .about__inner{
       display: flex;
       flex-direction: column;
       text-align: center;
       align-items: center;
        
    }
    .about-column__last{
        margin-left: 0;
    }

    .about-column__margin{
        margin-top: 0;
        padding-bottom: 0;
    }
  }
