.summit {
    background-image: url('../images/summit-bg.jpg');
    background-repeat: no-repeat;
    background-position: right top;
    background-size: auto;
    height: 850px;
    
    
    &__container {
       display: flex;
        align-items: center;
        height: 100%;
      
    }
  
    &__text {
        max-width: 507px;
        margin-bottom: 30px;
        font-weight: 300;
        line-height: 18px; 
        letter-spacing: 0.6px;
        &:last-child {
            margin-bottom: 0;
        }
    }

    &__title {
        margin-bottom: 100px;
    }
  }

  @media(max-width:1750px){
    .summit__inner{
        max-width: 410px;
    }
  }
  @media(max-width:1440px){
    .summit__inner{
        max-width: 300px;
    }
  }

  @media(max-width:1280px) {

    .summit{
        background-position: center;
        background-size: cover;
        
    }

    .summit__container{
        display: flex;
        justify-content: center;
    }

    .summit__text{
        color: #fff;
        text-align: center;
       
    }

    .summit__title{
        margin-bottom: 30px;
        padding-left: 50px;
        & span {
            display: none;
        }
    }
  }
  
  @media(max-width:990px){
    .summit__inner{
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .summit__title{
        margin-bottom: 50px;
        & span {
            display: none;
        }
    }
  

  }
  

 