.team {

    padding: 200px 0 200px;

    &__inner {
       display: flex;
       text-align: center;
       gap: 105px;
    }
    &__item img{
        max-width: 100%;
        height: auto;
    }

    &__title {
       padding-bottom: 200px;

    }
    &__name {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 18px;
        letter-spacing: 0.6px;
        padding: 25px 0 5px;
    }
    &__position {
        line-height: 18px; 
        letter-spacing: 0.6px;
    }
  }

  @media(max-width:768px){

    .team{
        padding: 100px 0 100px;
    }
    .team__item{
        padding-bottom: 60px;
    }
    .team__inner{
        display: block;
    }
  }

 

  
