.title {
    text-align: center;
    font-family: raleway;
    font-size: 48px;
    color: var(--primary-color);
    font-weight: 600;
    letter-spacing: 0.6px;
    position: relative;

    &__decor{
        padding-top: 20px;
        width: 40px;
    }


  }

  @media(max-width: 768px) {
    .title {
        
        font-size: 38px;
    }
  }
  @media(max-width: 440px) {

    .about__title{
       margin-left: 35px;
    }
    .about__title span {
        display: none;
    }

  }